
.app{
    display:grid;
    grid-template-rows: 
            [header-start] 15vh
            [r1-start] minmax(-webkit-min-content,35vh)
            [r2-start] minmax(-webkit-min-content,35vh)
            [r3-start] minmax(-webkit-min-content,10vh)
            ;
    grid-template-rows: 
            [header-start] 15vh
            [r1-start] minmax(min-content,35vh)
            [r2-start] minmax(min-content,35vh)
            [r3-start] minmax(min-content,10vh)
            ;
          
        

    grid-template-columns: 
            [side-bar-start] 1fr 
            [side-bar-end] repeat(12,[col-start] 1fr [col-end])
            [ad-bar] 1fr;

    grid-row-gap: 1rem; 
 
}

.board__container{
    grid-row: 2/ span 2;
    grid-column: 1/-1;

    background-color: rgb(228, 70, 70);
    width: 100%;
    height: 100%;
}
.board{
    grid-row: 2/ span 2;
    grid-column: 1/-1;

    display:grid;
    grid-template-columns: repeat(3,1fr);
    grid-template-rows: repeat(3,1fr);
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
    align-self: center;
    justify-self: center;
    padding: 1rem;

    position: relative;
}



