.header{
    grid-row: 1/ 2;
    grid-column: 1/-1;
    background-color: rgba(255, 68, 0, 0.8);

    display: -ms-flexbox;

    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}
.header__title{
    font-size: 2rem;
    color:white;
}
.header__secondary{
    font-size: 1.4rem;
    color:white;
    display:block;
}


