.button{

    grid-row: 4/ span 1;
    grid-column: 7/9;
    -ms-flex-item-align: start;
        align-self: flex-start;
    justify-self: center;
    padding: 1rem 2rem;
    cursor: pointer;
    outline:none;
    border: 0;
    background: #FF6933;
    color: white;
    font-size: 1.4rem;
    content: 'Restart the Game';
}
.button:hover{
   background-color:  rgb(228, 70, 70);
   -webkit-transition: 1s ease-in;
   -o-transition: 1s ease-in;
   transition: 1s ease-in;
}
button:focus {outline:0;}


